/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
@include tablet {
.site-content {
margin: ($base__space-unit * 3) 175px $base__space-unit; 
position: relative;

	&:before,
	&:after {
		position: absolute;
		color: $color__brand-green;
		font-family: 'FontAwesome';
		font-size: 125px;
		line-height: 125px;
		top: 0;
	}
	&:before {
		content: '\f10d';
		left: -175px;
	}	
	&:after {
		content: '\f10e';
		right: -175px;
	}
}
}
/* Posts and pages */

.sticky {
	display: block;
}

.hentry {
	margin: 0 0 $base__space-unit;
}

.updated:not(.published){
	display: none;
}

.single .byline,
.group-blog .byline {
	display: inline;
}

.page-content,
.entry-content,
.entry-summary {
	margin: $base__space-unit 0 0;
}

.page-links {
	clear: both;
	margin: 0 0 $base__space-unit;
}

.quote-button {
	display: flex;
	justify-content: center;
	margin-top: 2.5rem;
}

@include tablet {
.archive .entry-content p, .blog .entry-content p, .single .entry-content p {
	font-size: 2.5rem;
}
}
.hfeed.site {
	margin: 0 auto;
	max-width: 1080px;
	padding: 2.5%;
	width: 100%;
}
.browse-archives {
	ul{
		display: flex;
		flex-wrap: wrap;
		margin-left: -3rem;
		li {
			list-style-type: none;
			font-size: 1.25rem;
			font-weight: 300;
			margin: 0.4rem;
		}
		a {
			text-decoration: none;
		}
	}
}	
label {
	display: flex;
	flex-direction: column; 
}
.archive .hentry {
  border-bottom: 1px dotted #fff;
}
.entry-meta {
	display: flex;
	align-items: center;
	.source {
		margin-left: 10px;
	}
}

